@tailwind base;
@tailwind components;
@tailwind utilities;

@font-face {
    font-family: 'Helvetica';
    src: url("./customeFonts/helvetica-compressed-5871d14b6903a.otf") format('ttf');
    font-style: normal;

}

.jost-400 {
    font-family: "Jost", sans-serif;
    font-optical-sizing: auto;
    font-weight: 400;
    font-style: normal;
}

.outfit {
    font-family: "Outfit", sans-serif;
}

.hide {
    opacity: 0;
    transition: opacity 0ms ease-in-out;
}

.show {
    opacity: 1;
    transition: opecity 900ms ease-in-out 900ms forwards;

}

@keyframes fadeIn {
    from {
        opacity: 0;
    }

    to {
        opacity: 1;
    }
}

@media (max-width : 640px) {


    .manubuttontopnavbar {
        z-index: 4;
        display: flex;
        align-items: center;
    }

    .main {
        overflow: hidden;
        height: auto;
    }

    .manubuttonsidenavbar {
        display: none;
    }

    .sidenavbarroot {
        position: fixed;
        height: 90vh;
        padding-top: 35%;
    }

    .Topnavbarroot {
        justify-content: initial;
        height: 4rem;
    }

    .chat-section-div {
        width: 90vw;
        height: 62vh;
    }

    .promptroot {
        z-index: 2;
        width: 90vw;
        height: 80%;
    }

    .cardchatsectionmain {
        margin-top: 1rem;
    }

    .chat-section-main-div {
        height: 67%;
    }

    .chatsectionintrolines span {
        font-size: 2.7rem;
    }

    .topnavbarbutton {
        display: none;
    }

    .topnavbarprofilemain {
        margin-left: 30%;
    }

    .cardroot {
        width: 45vh;
    }

    #card3 {
        display: none;

    }

    #card4 {
        display: none;
    }

    .promptdiv {
        position: relative;
        top: 0;
    }

    .sidenavbarroot {
        z-index: 3;
        width: 100%;
    }

    .geminilogotopnavbar{
        z-index: 4;
    }
    .geminilogotopnavbar img{
        display: none;
    }
}

@media (min-width : 640px) {
    .manubuttontopnavbar {
        display: none;
    }
}